import React, { Component } from 'react'
import firebase from '../firebase'

interface State {
    user: any,
    type: string
}
class Login extends Component<State> {
    state = {
        user: {
            displayName: "",
            email: ""
        },
        type: ""
    }
    componentDidMount() {
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                this.setState({ user: user });
                if (~this.state.user.email.indexOf("ship.sakaehigashi.ed.jp")) {
                    this.setState({ type: "loginSuccessful" });
                } else {
                    this.setState({ type: "wrongAccount" });
                }
            } else {
                this.setState({ type: "notLoggedIn" });
            }
        });
    }
    login() {
        const provider = new firebase.auth.GoogleAuthProvider().setCustomParameters({
            prompt: 'select_account'
        });
        firebase.auth().signInWithPopup(provider).then((result: any) => {
            const userMail = result.user.email;
            if (~userMail.indexOf("ship.sakaehigashi.ed.jp")) { } else {
                var user: any = firebase.auth().currentUser;
                user.delete();
            };
        });
    }
    loginWithDelete() {
        var user: any = firebase.auth().currentUser;
        user.delete().then(function () {
            const provider = new firebase.auth.GoogleAuthProvider().setCustomParameters({
                prompt: 'select_account'
            });
            firebase.auth().signInWithPopup(provider);
        });
    }
    logout() {
        firebase.auth().signOut();
    }
    status() {
        if (this.state.type === "loginSuccessful") {
            return (<div id="loginSuccessful">
                <h3>ユーザーデータ</h3>
                <p>※このデータは管理者や他人に直接見えるものではありません。</p>
                <h4>ユーザー名</h4>
                <p>{this.state.user.displayName}</p>
                <h4>メールアドレス</h4>
                <p>{this.state.user.email}</p>
                <button onClick={this.logout}>ログアウト</button>
            </div>)
        } else if (this.state.type === "wrongAccount") {
            return (<div id="wrongAccount">栄東で配られたアカウントでログインしてください。
                <button onClick={this.loginWithDelete}>ログインする</button>
            </div>)
        } else if (this.state.type === "notLoggedIn") {
            return (<div id="notLoggedIn">
                <button onClick={this.login}>ログイン</button>
                <p></p>
            </div>)
        }
    }
    render() {
        return (
            <div className="login">
                <h1>ログイン</h1>
                {this.status()}
            </div>
        );
    }
}

export default Login;